import React from 'react';
import {Button, Modal, Form, FormGroup, Col, FormControl, ControlLabel} from 'react-bootstrap';
require('./index.css');
const ChanPassModal = React.createClass({
  render() {
    var {is_chanPass,action} =this.props;
    console.log(this.props);
    const formInstance = (
      <Form horizontal>
        <FormGroup controlId="formHorizontalEmail">
          <Col componentClass={ControlLabel} sm={2}>
            
          </Col>
          <Col sm={8}>
            <FormControl type="password" placeholder="(6-16)"/>
          </Col>
        </FormGroup>

        <FormGroup controlId="formHorizontalPassword">
          <Col componentClass={ControlLabel} sm={2}>
            
          </Col>
          <Col sm={8}>
            <FormControl type="password" placeholder=""/>
          </Col>
        </FormGroup>
      </Form>
    );
    return (
      <div className="modal-container">
        <Modal
          show={is_chanPass}
          onHide={action.hideChanPass}
          container={this}
          aria-labelledby="contained-modal-title">
          <Modal.Header closeButton>
            <Modal.Title id="contained-modal-title"></Modal.Title>
          </Modal.Header>
          <Modal.Body>
            {formInstance}
          </Modal.Body>
          <Modal.Footer>
            <Button
              bsStyle="primary"
              style={{width:"80px",borderRadius:'5px'}}
              onClick={action.hideChanPass}></Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
});

export default ChanPassModal;



