import  goto from '../library/changeHash';
function clearLocalStorage() {
  if(localStorage){
    localStorage.removeItem('neuqst.token');
  }else{
    alert('localStorage,');
  }
}

export  function logout() {
  goto('/auth/start');
  // clearLocalStorage();
}

export function gotoReg() {
  goto('/auth/register');
  // clearLocalStorage();
}

export function gotoLogin() {
  goto('/auth/login');
  // clearLocalStorage();
}



